"use client";

import React, { useState, useEffect, useCallback } from "react";
import { useRouter } from "next/navigation";
import { AppMode } from "@/app/types";
import { DEFAULT_QUESTIONS } from "../../constants";
import useLocalStorage from "@/app/hooks/useLocalStorage";
import ToggleSwitch from "@/app/components/ToggleSwitch";
import Roulette from "@/app/components/Roulette";
import Slot from "@/app/components/Slot";
import { SettingsIcon } from "@/app/components/icons";
import ResultModal from "@/app/components/ResultModal";

export default function Page() {
  const router = useRouter();

  const [mode, setMode] = useLocalStorage<AppMode>("app-mode", AppMode.Roulette);
  const [questions] = useLocalStorage<string[]>("questions", DEFAULT_QUESTIONS);
  const [askedIndexes, setAskedIndexes] = useLocalStorage<number[]>("asked-indexes", []);
  const [isSpinning, setIsSpinning] = useState(false);
  const [selectedQuestion, setSelectedQuestion] = useState<string | null>(null);
  const [displayQuestions, setDisplayQuestions] = useState<string[]>([]);
  const [isResultOpen, setIsResultOpen] = useState(false);

  const getAvailableQuestions = useCallback(() => {
    return questions
      .map((q, i) => ({ question: q, index: i }))
      .filter((item) => !askedIndexes.includes(item.index));
  }, [questions, askedIndexes]);

  const handleStart = () => {
    if (isSpinning) return;

    const available = getAvailableQuestions();
    if (available.length === 0) {
      alert("すべての質問が出題されました！");
      return;
    }

    const finalDisplay = [...available].sort(() => Math.random() - 0.5).slice(0, 10);
    setDisplayQuestions(finalDisplay.map((a) => a.question));

    setIsSpinning(true);
    setSelectedQuestion(null);
  };

  // ルーレット停止後に呼ばれる
  const handleRouletteStop = (topQuestion: string) => {
    const resultIndex = questions.findIndex((q) => q === topQuestion);
    setSelectedQuestion(topQuestion);
    setAskedIndexes((prev) => [...prev, resultIndex]);
    setIsSpinning(false);
    setIsResultOpen(true);
  };

  const handleClear = () => {
    setAskedIndexes([]);
    setSelectedQuestion(null);
    alert("履歴をリセットしました。");
  };

  const handleToggleMode = useCallback(() => {
    setMode((prev) =>
      prev === AppMode.Roulette ? AppMode.Slot : AppMode.Roulette
    );
  }, [setMode]);

  const availableCount = getAvailableQuestions().length;
  const totalCount = questions.length;

  return (
    <div className="min-h-screen bg-gray-100 text-gray-800 flex flex-col items-center justify-center p-4 font-sans relative">
      <div className="absolute top-4 right-4">
        <button
          onClick={() => router.push("/admin")}
          disabled={isSpinning}
          className="text-gray-500 hover:text-blue-500 transition-colors disabled:opacity-50"
        >
          <SettingsIcon className="w-8 h-8" />
        </button>
      </div>

      <header className="text-center mb-8">
        <h1 className="text-4xl md:text-5xl font-bold text-gray-800">
          質問ルーレット
        </h1>
      </header>

      <main className="flex-grow flex flex-col items-center justify-center w-full">
        <Roulette
          questions={displayQuestions}
          isSpinning={isSpinning}
          onStop={handleRouletteStop}
        />
      </main>

      <footer className="w-full max-w-lg mt-8">
        <div className="flex items-center justify-center mb-6">
          <ToggleSwitch mode={mode} onToggle={handleToggleMode} />
        </div>
        <div className="flex flex-col sm:flex-row gap-4">
          <button
            onClick={handleStart}
            disabled={isSpinning || availableCount === 0}
            className="w-full bg-green-500 hover:bg-green-600 text-white font-bold py-3 px-6 rounded-lg text-lg"
          >
            {isSpinning ? "回転中..." : "スタート"}
          </button>
          <button
            onClick={handleClear}
            disabled={isSpinning}
            className="w-full bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-3 px-6 rounded-lg text-lg"
          >
            履歴をクリア
          </button>
        </div>
        <p className="text-center mt-4 text-gray-500">
          残り {availableCount} / {totalCount} 問
        </p>
      </footer>

      <ResultModal
        isOpen={isResultOpen}
        question={selectedQuestion}
        onClose={() => setIsResultOpen(false)}
      />
    </div>
  );
}
